import { products } from "@/lib/data"
import { FaStar, FaStarHalfAlt } from "react-icons/fa";
import CheckColor from "./checkColor";
import ChooseSize from "./chooseSize";
import { MinusIcon, PlusIcon } from "lucide-react";
import ChooseQuantity from "./chooseQuantity";

export async function generateStaticParams() {
    return products.map((product) => ({
        slug: product.id
    }))
}

export default function Page({ params }) {
    const product = products.find(p => p.id === params.slug)

    //rating
    const maxStars = 5;
    const rating = product.rating;

    const getStarIcon = (index) => {
        if (index + 1 <= Math.floor(rating)) {
            return <FaStar className="text-[#FFC633]" size={25} />;
        } else if (index < rating && rating % 1 >= 0.5) {
            return <FaStarHalfAlt className="text-[#FFC633]" size={25} />;
        } else {
            return null
        }
    };

    //check color

    return (
        <div className="flex flex-row h-screen mx-35 my-10">
            <div className="flex flex-row w-1/2  gap-5 p-5">
                <div className="right-0 md:order-2 order-1 w-[295px]">
                    <img src={product.pictures[0]} alt="mainpic" />
                </div>
                <div className="flex flex-col md:order-1 order-2 gap-3">
                    {product.pictures.map((ulr) => (
                        <img
                            className=" w-[152px]"
                            src={ulr} key={ulr} />)
                    )}
                </div>
            </div>
            <div className="w-1/2">
                <div>
                    <h1 className="uppercase font-extrabold text-4xl">{product.name}</h1>
                    <div className="flex items-center my-3 gap-1">
                        {[...Array(maxStars)].map((_, i) => (
                            <span key={i}>
                                {getStarIcon(i)}
                            </span>
                        ))}
                        <span className="ml-1 text-sm">{product.rating}/5</span>
                    </div>
                    {product.discount === 0 ?
                        <p className="mt-2 font-bold text-3xl">${product.price}</p> : (
                            <div className="flex items-center gap-2 py-2">
                                <p className=" font-bold text-3xl">${(product.price * (1 - product.discount / 100)).toFixed(0)}</p>
                                <p className="text-3xl line-through text-[#b7b7b7]">${product.price}</p>
                                <span className="text-2xl text-[#FF3333] bg-[#ffebeb] py-1 px-3 rounded-4xl">-{product.discount}%</span>
                            </div>
                        )}
                    <p className="my-4 text-lg text-gray-500 ">{product.description}</p>
                </div>
                <hr className="my-5 text-gray-300" />
                <CheckColor product={product} />
                <hr className="my-5 text-gray-300" />
                <ChooseSize product={product} />
                <hr className="my-5 text-gray-300" />
                <div>
                    <div className="flex justify-between">
                        <ChooseQuantity product={product} />
                        <button
                            className="cursor-pointer bg-black w-full text-white text-2xl rounded-full ml-6"
                        >Add to Cart</button>
                    </div>
                </div>
            </div>
        </div>
    )

}