import { MinusIcon, PlusIcon } from "lucide-react";
import { useState } from "react";

export default function ChooseQuantity({ product }) {
    const [quantity, setQuantity] = useState(0)

    return (<div className="flex w-fit gap-12 bg-[#f0f0f0] py-5 px-5 text-2xl rounded-full">
        <button className="cursor-pointer"><MinusIcon /></button>
        <p>{product.quantity}</p>
        <button className="cursor-pointer"><PlusIcon /></button>
    </div>)
}